import React from 'react';
import i18n from './i18n';
import profile from './profile';
import './App.css';

import padlockOpen from './padlock-open.svg';
import padlockClosed from './padlock-closed.svg';

function App() {
    const [unlocked, setUnlocked] = React.useState(false);
    return <div className="App">
        <div className="background">
            { !unlocked && <LockGlass onUnlock={() => setUnlocked(true)} /> }
            { unlocked && <>
                <div className="page">
                    <div>
                        <div style={{ flexDirection: 'column' }}>
                            <Glass small fromTop>
                                <GlassImage src={profile.img} />
                                <GlassTitle value="Hello, I'm Jordán ...\n... and this is my website." />
                            </Glass>
                            <Glass small fromTop>
                                <GlassTitle value="Empty glass ..." />
                            </Glass>
                        </div>
                        <div>
                            <Glass fromBottom>
                                <GlassTitle value="... or glass with content" />
                                <GlassContent>
                                    <p>TODO</p>
                                </GlassContent>
                            </Glass>
                        </div>
                    </div>
                </div>
            </> }
        </div>
    </div>
}

const GlassStates = {
    INITIAL: 'initial',
    HOLDING: 'holding',
    GO_UP: 'go-up'
}

const oneSecond = 1000;

function LockGlass({ onUnlock }) {
    const interval = React.useRef(-1);
    const glassRef = React.useRef(null);
    const [duration, setDuration] = React.useState(0);
    const [glassState, setGlassState] = React.useState(GlassStates.INITIAL);

    const start = () => {
        let seconds = 0;
        if (glassState !== GlassStates.INITIAL) {
            return;
        }

        interval.current = window.setInterval(() => {
            seconds++;
            if (seconds >= 0) {
                glassRef.current.classList.add('open')
            }
            setDuration(seconds * oneSecond);
        }, oneSecond);

        setGlassState(GlassStates.HOLDING);
    };

    const end = () => {
        if (interval.current) {
            window.clearInterval(interval.current);
        }
        if (duration >= 0) {
            setGlassState(GlassStates.GO_UP);
            setTimeout(onUnlock, oneSecond);
        } else {
            setDuration(0);
            setGlassState(GlassStates.INITIAL);
        }
    };

    return <Glass appear fromBottom ref={glassRef} start={start} end={end} classNames={[glassState]}>
        <div className="hint">
            <span className="hold">{i18n.glass.unlockText}</span>
            <span className="release">{i18n.glass.releaseText}</span>
            <img className="closed" src={padlockClosed} alt="closed" />
            <img className="open" src={padlockOpen} alt="opened" />
        </div>
        <div className="avatar">
            <div className="image"><img alt="Szalontai Jordán" src={profile.img} /></div>
            <h1>Szalontai Jordán</h1>
        </div>
    </Glass>;
}

function _Glass(props, glassRef) {
    const {
        start = noop,
        end = noop,
        classNames = [],
        fromBottom,
        fromTop,
        appear,
        small,
        children
    } = props;

    const glassClasses = [
        ...classNames,
        'glass',
        appear && 'appear',
        fromBottom && 'from-bottom',
        fromTop && 'from-top',
        small && 'small'
    ].filter(Boolean).join(' ');

    return <div
        onTouchStart={start}
        onMouseDown={start}
        onMouseUp={end}
        onTouchEnd={end}
        className={glassClasses}
        ref={glassRef}>{children}</div>;
}

const Glass = React.forwardRef(_Glass);

function GlassTitle(props) {
    const { value } = props;
    return <h1 className="glass-title">{value.split('\\n').map((text, key) => <div key={key}>{text}</div>)}</h1>;
}

function GlassImage(props) {
    const { src } = props;
    return <>
        <img className="glass-image" src={src} alt="Image" />
    </>;
}

function GlassContent(props) {
    return <>
        <div className="glass-content-white" />
        <div className="glass-content">
            {props.children}
        </div>
    </>;
}

function noop() {
    /* */
}

export default App;
